.transport-list {
  display: flex;
  margin: 0;
  padding: 0;

  list-style: none;
}

.transport-list__item {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;

  &--avia {
    margin-right: 17px;
  }

  &--bus {
    margin-right: 13px;
  }

  &--bike {
    margin-right: 10px;
  }

  &--filter,
  &--form {
    width: 32px;
    height: 32px;
    margin-right: 0;

    background-color: #ffffff;
  }

  &--catalog {
    width: 32px;
    height: 32px;
    margin-right: 0;

    background-color: $bg-light;
  }

  &:hover > .transport-list__tooltip {
    display: block;
    z-index: 10;
  }

  border-radius: 50%;
}

.transport-list__tooltip {
  display: none;
  position: absolute;
  top: 26px;
  left: -50px;

  padding: 16px 29px;

  font-size: 20px;
  line-height: 24px;
  color: #ffffff;

  background-color: $bg-tooltip;

  border-radius: 3px;
}

.transport-list__icon {
  fill: $transport-icon;

  &--active {
    fill: $title-dark;
  }
}

.transport-list__input {
  &:checked + .transport-list__item > .transport-list__icon {
    fill: $title-dark;
  }
}

@media (min-width: $tablet-width) {
  .transport-list {
    &--form {
      margin-bottom: 20px;
    }
  }

  .transport-list__item {
    &--filter,
    &--form {
      width: 42px;
      height: 42px;
    }

    &--filter > .transport-list__icon,
    &--form > .transport-list__icon {
      transform: scale(1.3);
    }

    &--catalog {
      background-color: transparent;
    }

    &--catalog svg {
      transform: scale(1.3);
    }

    &--form {
      margin-right: 8px;
    }
  }
}

@media (min-width: $desktop-width) {
  .transport-list__item {
    &--form {
      margin-right: 0;
    }

    &--filter,
    &--form {
      &:hover,
      &:focus {
        box-shadow: 0 2px 10px 0 rgba(0, 0, 0, 0.2);
      }

      &:active {
        opacity: 0.3;
      }
    }
  }
}
