.contacts {
  display: flex;
  flex-direction: column;

  font-size: 16px;
  line-height: 22px;
  font-weight: 700;
  text-transform: uppercase;
}

.contacts__link {
  position: relative;
  display: flex;
  align-items: center;
}

.contacts__link-icon {
  width: 45px;
  height: 45px;
  margin-right: 15px;
  padding: 13px;

  background-color: $bg-contacts;

  fill: #ffd74b;

  border-radius: 50%;
}

@media (min-width: $tablet-width) {
  .contacts {
    font-size: 28px;
    line-height: 28px;
  }

  .contacts__link-icon {
    width: 68px;
    height: 68px;
    margin-right: 23px;
    padding: 20px;

    & svg {
      width: 27px;
      height: 27px;
    }
  }
}

@media (min-width: $desktop-width) {
  .contacts__link {
    &:hover,
    &:focus {
      color: $bg-button-yellow;
    }

    &:hover .contacts__link-icon > svg,
    &:focus .contacts__link-icon > svg {
      fill: $bg-button-yellow;
    }

    &:active {
      color: rgba($bg-button-yellow, 0.3);
    }

    &:active .contacts__link-icon > svg {
      fill: rgba($bg-button-yellow, 0.3);
    }
  }

  .contacts__link-icon {
    width: 90px;
    height: 90px;
    margin-right: 30px;
    padding: 27px;

    & svg {
      width: 36px;
      height: 36px;
    }
  }
}
